/**
 * Compensation & Benefits Page JavaScript
 * 
 * This file contains the JavaScript for the Compensation & Benefits page.
 */

// Import components
import Accordion from '../components/accordion';
import Testimonials from '../components/testimonials';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Compensation & Benefits page initialized');
  
  // Initialize accordion
  Accordion.init({
    triggerSelector: '.faq-question',
    contentSelector: '.faq-answer',
    closeOthers: true,
    animateHeight: true
  });
  
  // Initialize testimonials
  Testimonials.init({
    testimonialSelector: '.testimonial',
    dotSelector: '.testimonial-dot',
    activeClass: 'active',
    autoRotate: true,
    autoRotateInterval: 5000
  });
  
  // Set up salary calculator
  setupSalaryCalculator();
  
  // Set up benefits comparison tool
  setupBenefitsComparison();
}

/**
 * Set up salary calculator
 */
function setupSalaryCalculator() {
  const calculatorForm = document.querySelector('.salary-calculator-form');
  const resultContainer = document.querySelector('.salary-calculator-result');
  
  if (calculatorForm && resultContainer) {
    calculatorForm.addEventListener('submit', function(e) {
      e.preventDefault();
      
      // Get form data
      const industry = calculatorForm.querySelector('select[name="industry"]').value;
      const role = calculatorForm.querySelector('select[name="role"]').value;
      const experience = parseInt(calculatorForm.querySelector('select[name="experience"]').value, 10);
      const location = calculatorForm.querySelector('select[name="location"]').value;
      
      // Calculate salary range
      const salaryRange = calculateSalaryRange(industry, role, experience, location);
      
      // Display result
      displaySalaryResult(salaryRange);
    });
  }
  
  /**
   * Calculate salary range based on inputs
   */
  function calculateSalaryRange(industry, role, experience, location) {
    // This would normally come from an API or database
    // For now, we'll use a simple calculation
    
    // Base salary ranges by industry and role
    const baseSalaries = {
      'tech': {
        'developer': { min: 70000, max: 120000 },
        'manager': { min: 90000, max: 150000 },
        'executive': { min: 130000, max: 200000 }
      },
      'healthcare': {
        'specialist': { min: 60000, max: 110000 },
        'manager': { min: 80000, max: 140000 },
        'executive': { min: 120000, max: 190000 }
      },
      'finance': {
        'analyst': { min: 65000, max: 115000 },
        'manager': { min: 85000, max: 145000 },
        'executive': { min: 125000, max: 195000 }
      }
    };
    
    // Location multipliers
    const locationMultipliers = {
      'northeast': 1.15,
      'midwest': 0.9,
      'south': 0.85,
      'west': 1.1,
      'remote': 1.0
    };
    
    // Experience multipliers
    const experienceMultiplier = 1 + (experience * 0.05); // 5% increase per year of experience
    
    // Get base salary range
    let baseSalary = { min: 60000, max: 100000 }; // Default
    if (baseSalaries[industry] && baseSalaries[industry][role]) {
      baseSalary = baseSalaries[industry][role];
    }
    
    // Apply multipliers
    const locationMultiplier = locationMultipliers[location] || 1.0;
    
    const minSalary = Math.round(baseSalary.min * experienceMultiplier * locationMultiplier);
    const maxSalary = Math.round(baseSalary.max * experienceMultiplier * locationMultiplier);
    
    return { min: minSalary, max: maxSalary };
  }
  
  /**
   * Display salary calculation result
   */
  function displaySalaryResult(salaryRange) {
    const resultContainer = document.querySelector('.salary-calculator-result');
    
    if (resultContainer) {
      // Format salary numbers
      const minSalary = salaryRange.min.toLocaleString('en-US', { style: 'currency', currency: 'USD', maximumFractionDigits: 0 });
      const maxSalary = salaryRange.max.toLocaleString('en-US', { style: 'currency', currency: 'USD', maximumFractionDigits: 0 });
      
      resultContainer.innerHTML = `
        <h3>Estimated Salary Range</h3>
        <div class="salary-range">${minSalary} - ${maxSalary}</div>
        <p class="salary-disclaimer">This is an estimated range based on industry averages. Actual salaries may vary based on company size, specific skills, and other factors.</p>
        <div class="salary-tips">
          <h4>Negotiation Tips</h4>
          <ul>
            <li>Research the company and industry thoroughly before negotiating</li>
            <li>Consider the total compensation package, not just the base salary</li>
            <li>Be prepared to discuss your value and specific achievements</li>
            <li>Practice your negotiation conversation beforehand</li>
          </ul>
        </div>
      `;
      
      resultContainer.style.display = 'block';
    }
  }
}

/**
 * Set up benefits comparison tool
 */
function setupBenefitsComparison() {
  const comparisonForm = document.querySelector('.benefits-comparison-form');
  const resultContainer = document.querySelector('.benefits-comparison-result');
  
  if (comparisonForm && resultContainer) {
    comparisonForm.addEventListener('submit', function(e) {
      e.preventDefault();
      
      // Get form data
      const militarySalary = parseFloat(comparisonForm.querySelector('input[name="military-salary"]').value) || 0;
      const militaryHousing = parseFloat(comparisonForm.querySelector('input[name="military-housing"]').value) || 0;
      const militaryHealthcare = parseFloat(comparisonForm.querySelector('input[name="military-healthcare"]').value) || 0;
      const militaryRetirement = parseFloat(comparisonForm.querySelector('input[name="military-retirement"]').value) || 0;
      
      const civilianSalary = parseFloat(comparisonForm.querySelector('input[name="civilian-salary"]').value) || 0;
      const civilianBonus = parseFloat(comparisonForm.querySelector('input[name="civilian-bonus"]').value) || 0;
      const civilianHealthcare = parseFloat(comparisonForm.querySelector('input[name="civilian-healthcare"]').value) || 0;
      const civilian401k = parseFloat(comparisonForm.querySelector('input[name="civilian-401k"]').value) || 0;
      
      // Calculate totals
      const militaryTotal = militarySalary + militaryHousing + militaryHealthcare + militaryRetirement;
      const civilianTotal = civilianSalary + civilianBonus + civilianHealthcare + civilian401k;
      
      // Display comparison
      displayBenefitsComparison(
        { salary: militarySalary, housing: militaryHousing, healthcare: militaryHealthcare, retirement: militaryRetirement, total: militaryTotal },
        { salary: civilianSalary, bonus: civilianBonus, healthcare: civilianHealthcare, retirement: civilian401k, total: civilianTotal }
      );
    });
  }
  
  /**
   * Display benefits comparison result
   */
  function displayBenefitsComparison(military, civilian) {
    const resultContainer = document.querySelector('.benefits-comparison-result');
    
    if (resultContainer) {
      // Format currency numbers
      const formatCurrency = value => value.toLocaleString('en-US', { style: 'currency', currency: 'USD', maximumFractionDigits: 0 });
      
      // Calculate difference and percentage
      const difference = civilian.total - military.total;
      const percentDifference = military.total > 0 ? Math.round((difference / military.total) * 100) : 0;
      
      resultContainer.innerHTML = `
        <h3>Compensation Comparison</h3>
        <div class="comparison-summary">
          <div class="military-summary">
            <h4>Military Package</h4>
            <div class="total-value">${formatCurrency(military.total)}</div>
          </div>
          <div class="comparison-arrow">
            <i class="fas fa-${difference >= 0 ? 'arrow-right' : 'arrow-left'}"></i>
            <div class="difference-value ${difference >= 0 ? 'positive' : 'negative'}">
              ${formatCurrency(Math.abs(difference))} (${Math.abs(percentDifference)}%)
            </div>
          </div>
          <div class="civilian-summary">
            <h4>Civilian Package</h4>
            <div class="total-value">${formatCurrency(civilian.total)}</div>
          </div>
        </div>
        
        <div class="comparison-details">
          <table class="comparison-table">
            <thead>
              <tr>
                <th>Component</th>
                <th>Military</th>
                <th>Civilian</th>
                <th>Difference</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Base Salary</td>
                <td>${formatCurrency(military.salary)}</td>
                <td>${formatCurrency(civilian.salary)}</td>
                <td class="${civilian.salary - military.salary >= 0 ? 'positive' : 'negative'}">
                  ${formatCurrency(civilian.salary - military.salary)}
                </td>
              </tr>
              <tr>
                <td>Housing/Bonus</td>
                <td>${formatCurrency(military.housing)}</td>
                <td>${formatCurrency(civilian.bonus)}</td>
                <td class="${civilian.bonus - military.housing >= 0 ? 'positive' : 'negative'}">
                  ${formatCurrency(civilian.bonus - military.housing)}
                </td>
              </tr>
              <tr>
                <td>Healthcare</td>
                <td>${formatCurrency(military.healthcare)}</td>
                <td>${formatCurrency(civilian.healthcare)}</td>
                <td class="${civilian.healthcare - military.healthcare >= 0 ? 'positive' : 'negative'}">
                  ${formatCurrency(civilian.healthcare - military.healthcare)}
                </td>
              </tr>
              <tr>
                <td>Retirement</td>
                <td>${formatCurrency(military.retirement)}</td>
                <td>${formatCurrency(civilian.retirement)}</td>
                <td class="${civilian.retirement - military.retirement >= 0 ? 'positive' : 'negative'}">
                  ${formatCurrency(civilian.retirement - military.retirement)}
                </td>
              </tr>
              <tr class="total-row">
                <td><strong>Total</strong></td>
                <td><strong>${formatCurrency(military.total)}</strong></td>
                <td><strong>${formatCurrency(civilian.total)}</strong></td>
                <td class="${difference >= 0 ? 'positive' : 'negative'}">
                  <strong>${formatCurrency(difference)}</strong>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        
        <div class="comparison-notes">
          <h4>Important Considerations</h4>
          <ul>
            <li>Remember to factor in tax implications, which may differ between military and civilian compensation</li>
            <li>Consider non-monetary benefits like work-life balance, job security, and career growth opportunities</li>
            <li>Evaluate the long-term value of retirement benefits and healthcare coverage</li>
          </ul>
        </div>
      `;
      
      resultContainer.style.display = 'block';
    }
  }
}

// Initialize the page when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
